
import { CstNode } from 'chevrotain';
import { CalxLexer, CalxParser } from './Parser';
import { SharedContext } from '../SharedContext';

const parserInstance = new CalxParser();

class CalxInterpreter extends parserInstance.getBaseCstVisitorConstructorWithDefaults() {
    private context?: SharedContext;

    constructor() {
        super();
        this.validateVisitor();
    }

    setContext(context: SharedContext) {
        this.context = context;
    }

    expression(ctx: any) {
        console.log('Visiting expression');

        return this.visit(ctx.additionExpression[0]);
    }

    arrayFormula(ctx: any) {
        console.log('Visiting arrayFormula');
    }

    comparisonExpression(ctx: any) {
        console.log('Visiting comparisonExpression');
    }

    additionExpression(ctx: any) {
        console.log('Visiting additionExpression');
    }

    concatenationExpression(ctx: any) {
        console.log('Visiting concatenationExpression');
    }

    multiplicationExpression(ctx: any) {
        console.log('Visiting multiplicationExpression');
    }

    exponentiationExpression(ctx: any) {
        console.log('Visiting exponentiationExpression');
    }

    unaryExpression(ctx: any) {
        console.log('Visiting unaryExpression');
    }

    atomicExpression(ctx: any) {
        console.log('Visiting atomicExpression');
    }

    ifFunctionCall(ctx: any) {
        console.log('Visiting ifFunctionCall');
    }

    functionCall(ctx: any) {
        console.log('Visiting functionCall');
    }

    ifErrorFunctionCall(ctx: any) {
        console.log('Visiting ifErrorFunctionCall');
    }

    ifsFunctionCall(ctx: any) {
        console.log('Visiting ifsFunctionCall');
    }

    switchFunctionCall(ctx: any) {
        console.log('Visiting switchFunctionCall');
    }

    arrayExpression(ctx: any) {
        console.log('Visiting arrayExpression');
    }

    arrayRow(ctx: any) {
        console.log('Visiting arrayRow');
    }

    parseInput(inputText: string) {
        const lexResult = CalxLexer.tokenize(inputText);
        parserInstance.input = lexResult.tokens;

        const cst = parserInstance.expression();

        if (parserInstance.errors.length > 0) {
            throw Error('Parsing errors detected');
        }

        return this.visit(cst);
    }

    parseCst(cst: CstNode) {
        return this.visit(cst);
    }
}

export { CalxInterpreter };
export default CalxInterpreter;